/* Custom Javascript code */


  /**
   * Utility methods
   * */
  var qs = function (class_name, b_getall) {
    return (b_getall === true) ? document.querySelectorAll(class_name) : document.querySelector(class_name);
  };

  var addClass = function (ele, class_name) {
    ele.classList.add(class_name);
  };

  var removeClass = function (ele, class_name) {
    ele.classList.remove(class_name);
  };

  if(document.readyState === "complete" || (document.readyState !== "loading" && !document.documentElement.doScroll)){
    callback();
  }else{
    document.addEventListener("DOMContentLoaded", callback);
  }

  /**
   * callback code executed on load of document
   */
  function callback() {

    /**
     * DOM event bindings
     * */
    qs('.js-search-icon').addEventListener('click', function () {
      addClass(this, 'is-hidden');
      removeClass(qs('.js-close-icon'), 'is-hidden');
      removeClass(qs('.js-search'), 'is-hidden');
      qs('.search .search__input').focus();
    });

    qs('.js-close-icon').addEventListener('click', function () {
      addClass(this, 'is-hidden');
      removeClass(qs('.js-search-icon'), 'is-hidden');
      addClass(qs('.js-search'), 'is-hidden');
    });

    qs('.js-burger-icon').addEventListener('click', function () {
      if (this.className.indexOf('is-fixed') > -1) {
        removeClass(this, 'is-fixed');
        removeClass(this, 'show-cross');
        removeClass(qs('.js-mobile-nav'), 'is-open');
        removeClass(qs('.js-mobile-overlay'), 'is-visible');
      } else {
        addClass(this, 'is-fixed');
        addClass(this, 'show-cross');
        addClass(qs('.js-mobile-nav'), 'is-open');
        addClass(qs('.js-mobile-overlay'), 'is-visible');
      }
    });

    qs('.js-header__arrow').addEventListener('click', function () {
      var height = window.screen.height === 320 ? window.screen.height - 50 : 330;
      window.scrollBy({top: height, left: 0, behavior: 'smooth'});
      return false;
    });
    qs('.totop__link').addEventListener('click', function () {
      window.scroll({top: 0, left: 0, behavior: 'smooth'});
    });

    /**
     * window event change opacity on 'header__detail'
     */
    window.addEventListener('scroll', function () {
      var height = window.screen.height === 320 ? window.screen.height : 380;
      var movement = (1 - (parseInt(this.scrollY / (0.1 * height)) / 10));
      var header = qs('.header-detail').style.opacity = movement.toFixed(1);

      var top = qs('.totop');

      if (this.scrollY >= (height - 50)) {
        addClass(top, 'js-totop');
      } else {
        if (top.className.indexOf('js-totop') > -1) {
          removeClass(top, 'js-totop');
        }
      }

      var sitenavWrapper = qs('.site-nav-wrapper');

      if (this.scrollY >= (25)) {
        addClass(sitenavWrapper, 'is-dropdown');
      } else {
        if (this.scrollY <= (25)) {
          if (sitenavWrapper.className.indexOf('is-dropdown') >= -1) {
            removeClass(sitenavWrapper, 'is-dropdown');
          }
        }
      }

    });

    /**
     * interrogates the twitter iframe and builds the twitter list
     * done in native to minimise use of jQuery - maybe use mobile version?
     */
    var count = [0, 0];
    var outside = setInterval(function () {

      var twitterIframe = document.getElementById('twitter-widget-0');

      (count[0] > 10 || twitterIframe !== null) ? clearInterval(outside) : count[0]++;

      if (twitterIframe !== null) {
        var inside = setInterval(function () {

          var docBody = twitterIframe.contentWindow.document.body,
            tweets = docBody.querySelectorAll('.timeline-Tweet-text'),
            post_dates = docBody.querySelectorAll('.dt-updated'),
            li_list = '';

          (count[1] > 10 || tweets.length > 1) ? clearInterval(inside) : count[1]++;

          if (tweets.length > 1) {

            for (var i = 0; i < tweets.length; i++) {

              li_list += '<li class="tweets__item"><span class="fa fa-twitter"></span><p class="item__desc">{desc}</p><p class="item__date">{date}</p></li>'
                .replace('{desc}', function (tweet) {

                  for (var h = 0; h < tweet.childNodes.length; h++) {
                    var node = tweet.childNodes[h];
                    if (node.nodeType === Node.ELEMENT_NODE) {
                      // removes nodes not needed (clean up)
                      node.removeAttribute('data-expanded-url');
                      node.removeAttribute('data-scribe');
                      node.removeAttribute('dir');
                      node.removeAttribute('data-query-source');
                    }
                  }
                  return tweet.innerHTML;
                }(tweets[i]))
                .replace('{date}', post_dates[i].getAttribute('aria-label'));
            }
            document.querySelector('.js-tweets').innerHTML = li_list;
          }

        }, 500);
      } else {
        console.log('unable to get tweets')
      }
    }, 500);

  }
